import React, { useEffect, useState } from "react";
import Grid from "@mui/material/Grid";
import _break from "../../img/svg/break2.svg";
import quadretto from "../../img/quadretto.png";
import _top from "../../img/svg/topClear.svg";

const rootStyle = {
  marginRight: 0,
  marginLeft: 0,
  marginTop: "-1vh",
  backgroundSize: "cover",
  background:
    "linear-gradient(0deg, rgba(164,175,198,1) 0%, rgba(217,217,210,1) 100%)",
};

const textStyle = {
  color: "#102B4C",
  // textShadow: "2px 2px #102B4C",
  fontWeight: "bold",
};

const ImgStyle = {
  marginRight: 0,
  marginLeft: 0,
  marginTop: "-35vh",
  backgroundSize: "cover",
};

function Invito() {
  const [width, setWidth] = useState(window.innerWidth);
  const [topWidth, setTopWidth] = useState({
    width: 340,
  });

  useEffect(() => {
    if (width < 340) {
      setTopWidth({
        width: width - 30,
      });
    } else {
      setTopWidth({
        width: 340,
      });
    }
  }, [width]);

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <>
      <img src={_break} alt="break" style={ImgStyle} />
      <div style={rootStyle}>
        <Grid
          container
          direction="column"
          justify="space-between"
          justifyContent="center"
          alignItems="center"
        >
          <Grid item>
            <img src={quadretto} alt="quadretto" />
          </Grid>
          <Grid item>
            <div style={{ fontSize: "6em", ...textStyle }}>Save</div>
          </Grid>
          <Grid item style={{ marginTop: "-2%" }}>
            <div style={{ fontSize: "3em", ...textStyle }}>the</div>
          </Grid>
          <Grid item style={{ marginTop: "-2%" }}>
            <div style={{ fontSize: "6em", ...textStyle }}>Date</div>
          </Grid>
          <Grid item>
            <img
              src={_top}
              alt="top"
              style={{ fill: "#D9D9D2", ...topWidth }}
            />
          </Grid>
          <Grid item>
            <div style={{ fontSize: "3em", ...textStyle }}>27 May 2023</div>
          </Grid>
          <Grid item>
            <div style={{ fontSize: "2em", ...textStyle }}>alle ore 16:30</div>
          </Grid>
          <Grid item>
            <img
              src={_top}
              alt="top"
              style={{
                fill: "#D9D9D2",
                transform: "rotate(180deg)",
                ...topWidth,
              }}
            />
          </Grid>
          <Grid item>
            <p
              style={{ fontSize: "2em", fontWeight: "bold", color: "#102B4C" }}
            >
              La cerimonia si terr presso il
            </p>
          </Grid>
          <Grid item style={{ marginTop: "-4%" }}>
            <p
              style={{ fontSize: "2.5em", fontWeight: "bold", color: "#102B4C" }}
            >
              Santuatio Santa Maria del Canneto
            </p>
          </Grid>
          <Grid item style={{ marginTop: "-4%" }}>
            <p
              style={{ fontSize: "2em", fontWeight: "bold", color: "#102B4C" }}
            >
              (Gallipoli)
            </p>
          </Grid>
          <Grid item>
            <img
              src={quadretto}
              alt="quadretto"
              style={{ transform: "rotate(180deg)" }}
            />
          </Grid>
        </Grid>
      </div>
    </>
  );
}

export { Invito };
